.slider-photographie{
  margin: 0 auto;
  width: 100%;
  height: 650px;
  object-fit:cover;
}
.slider-wrapper {
  width: 100%;
  max-width: 2000px;
  margin: 0 auto; /* Pour centrer horizontalement */
 
  display: block;
  align-content: center;
}
.slick-prev{
  left: 20px !important; 
  z-index: 1 !important;
}

.slick-next{
  right: 25px !important;
}
